import { RecordCount } from 'components/RecordCount';
import { Title } from 'components/Title';

export default function HomePage() {
	
	return (
		<main id="HomePage" className="main-page">
<section className="page-section q-pa-md" >
    <div className="container-fluid">
        <div className="grid ">
            <div className="col comp-grid" >
                <Title title="Home"   titleClass="text-2xl font-bold" subTitleClass="text-sm text-500"       />
            </div>
            <div className="col-sm-4 col-md-4 col-12 comp-grid" >
                <RecordCount title="Walikelas" description="Total Walikelas" icon="pi pi-user" apiPath="components_data/getcount_walikelas" link={`/#`}     />
            </div>
            <div className="col-sm-4 col-md-4 col-12 comp-grid" >
                <RecordCount title="Siswa" description="Total Siswa" icon="pi pi-users" apiPath="components_data/getcount_siswa" link={`/#`}     />
            </div>
            <div className="col-sm-4 col-md-4 col-12 comp-grid" >
                <RecordCount title="Kelas" description="Total Kelas" icon="pi pi-building" apiPath="components_data/getcount_kelas" link={`/#`}     />
            </div>
            <div className="col-sm-4 col-md-4 col-12 comp-grid" >
                <RecordCount title="Jurusan" description="Total Jurusan" icon="pi pi-bookmark-fill" apiPath="components_data/getcount_jurusan" link={`/#`}     />
            </div>
        </div>
    </div>
</section>
		</main>
	);
}
<style scoped>
</style>
