import { Routes, Route } from 'react-router-dom';

import IndexLayout from 'layouts/IndexLayout';
import MainLayout from 'layouts/MainLayout';
import JurusanList from 'pages/jurusan/List';
import JurusanView from 'pages/jurusan/View';
import JurusanAdd from 'pages/jurusan/Add';
import JurusanEdit from 'pages/jurusan/Edit';
import KelasList from 'pages/kelas/List';
import KelasView from 'pages/kelas/View';
import KelasAdd from 'pages/kelas/Add';
import KelasEdit from 'pages/kelas/Edit';
import SiswaList from 'pages/siswa/List';
import SiswaView from 'pages/siswa/View';
import SiswaAdd from 'pages/siswa/Add';
import SiswaEdit from 'pages/siswa/Edit';
import WalikelasList from 'pages/walikelas/List';
import WalikelasView from 'pages/walikelas/View';
import WalikelasAdd from 'pages/walikelas/Add';
import WalikelasEdit from 'pages/walikelas/Edit';
import HomePage from './pages/home/HomePage';
import IndexPages from './pages/index';
import ErrorPages from './pages/errors';
import 'primereact/resources/primereact.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import 'assets/styles/layout.scss';
const App = () => {
	return (
		<Routes>
			<Route element={<MainLayout />}>
				<Route path="/" element={<HomePage />} />
				<Route path="/home" element={<HomePage />} />
				

				{/* jurusan pages routes */}
				<Route path="/jurusan" element={<JurusanList />} />
				<Route path="/jurusan/:fieldName/:fieldValue" element={<JurusanList />} />
				<Route path="/jurusan/index/:fieldName/:fieldValue" element={<JurusanList />} />
				<Route path="/jurusan/view/:pageid" element={<JurusanView />} />
				<Route path="/jurusan/add" element={<JurusanAdd />} />
				<Route path="/jurusan/edit/:pageid" element={<JurusanEdit />} />

				{/* kelas pages routes */}
				<Route path="/kelas" element={<KelasList />} />
				<Route path="/kelas/:fieldName/:fieldValue" element={<KelasList />} />
				<Route path="/kelas/index/:fieldName/:fieldValue" element={<KelasList />} />
				<Route path="/kelas/view/:pageid" element={<KelasView />} />
				<Route path="/kelas/add" element={<KelasAdd />} />
				<Route path="/kelas/edit/:pageid" element={<KelasEdit />} />

				{/* siswa pages routes */}
				<Route path="/siswa" element={<SiswaList />} />
				<Route path="/siswa/:fieldName/:fieldValue" element={<SiswaList />} />
				<Route path="/siswa/index/:fieldName/:fieldValue" element={<SiswaList />} />
				<Route path="/siswa/view/:pageid" element={<SiswaView />} />
				<Route path="/siswa/add" element={<SiswaAdd />} />
				<Route path="/siswa/edit/:pageid" element={<SiswaEdit />} />

				{/* walikelas pages routes */}
				<Route path="/walikelas" element={<WalikelasList />} />
				<Route path="/walikelas/:fieldName/:fieldValue" element={<WalikelasList />} />
				<Route path="/walikelas/index/:fieldName/:fieldValue" element={<WalikelasList />} />
				<Route path="/walikelas/view/:pageid" element={<WalikelasView />} />
				<Route path="/walikelas/add" element={<WalikelasAdd />} />
				<Route path="/walikelas/edit/:pageid" element={<WalikelasEdit />} />
			</Route>
			<Route exact element={<IndexLayout />}>
				<Route path="/*" element={<IndexPages />} />
				<Route path="/error/*" element={<ErrorPages />} />
			</Route>
		</Routes>
	);
}
export default App;
