import { Formik, Form, ErrorMessage } from 'formik';
import * as yup from 'yup';
import { Button } from 'primereact/button';
import { DataSource } from 'components/DataSource';
import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import { ProgressSpinner } from 'primereact/progressspinner';
import { Title } from 'components/Title';
import { Uploader } from 'components/Uploader';
import useApp from 'hooks/useApp';

import useAddPage from 'hooks/useAddPage';
const SiswaAddPage = (props) => {
		const app = useApp();
	
	//form validation rules
	const validationSchema = yup.object().shape({
		nis: yup.number().required().label("Nis"),
		nama: yup.string().required().label("Nama"),
		jenkel: yup.string().required().label("Jenkel"),
		kelas: yup.string().required().label("Kelas"),
		jurusan: yup.string().required().label("Jurusan"),
		wali_kelas: yup.string().required().label("Wali Kelas"),
		foto_siswa: yup.string().required().label("Foto Siswa")
	});
	
	//form default values
	const formDefaultValues = {
		nis: '', 
		nama: '', 
		jenkel: '', 
		kelas: '', 
		jurusan: '', 
		wali_kelas: '', 
		foto_siswa: '', 
	}
	
	//page hook where logics resides
	const pageController =  useAddPage({ props, formDefaultValues, afterSubmit });
	
	// destructure and grab what the page needs
	const { formData, resetForm, handleSubmit, submitForm, pageReady, loading, saving, inputClassName } = pageController;
	
	//event raised after form submit
	function afterSubmit(response){
		app.flashMsg(props.msgTitle, props.msgAfterSave);
		resetForm();
		if(app.isDialogOpen()){
			app.closeDialogs(); // if page is open as dialog, close dialog
		}
		else if(props.redirect) {
			app.navigate(`/siswa`);
		}
	}
	
	// page loading form data from api
	if(loading){
		return (
			<div className="p-3 text-center">
				<ProgressSpinner style={{width:'50px', height:'50px'}} />
			</div>
		);
	}
	
	//page has loaded any required data and ready to render
	if(pageReady){
		return (
<main id="SiswaAddPage" className="main-page">
    { (props.showHeader) && 
    <section className="page-section mb-3" >
        <div className="container">
            <div className="grid align-items-center">
                { !props.isSubPage && 
                <div className="col-fixed " >
                    <Button onClick={() => app.navigate(-1)} label=""  className="p-button p-button-text " icon="pi pi-arrow-left"  />
                </div>
                }
                <div className="col " >
                    <Title title="Form Input Data  Siswa"   titleClass="text-2xl text-primary font-bold" subTitleClass="text-sm text-500"      separator={false} />
                </div>
            </div>
        </div>
    </section>
    }
    <section className="page-section " >
        <div className="container">
            <div className="grid ">
                <div className="md:col-9 sm:col-12 comp-grid" >
                    <div >
                        <Formik initialValues={formData} validationSchema={validationSchema} onSubmit={(values, actions) =>submitForm(values)}>
                            {(formik) => 
                            <>
                            <Form className={`${!props.isSubPage ? 'card ' : ''}`}>
                                <div className="grid">
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Nis *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <InputText name="nis"  onChange={formik.handleChange}  value={formik.values.nis}   label="Nis" type="number" placeholder="Nomor Induk Siswa"  min={0}  step="any"    className={inputClassName(formik?.errors?.nis)} />
                                                <ErrorMessage name="nis" component="span" className="p-error" />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Nama *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <InputText name="nama"  onChange={formik.handleChange}  value={formik.values.nama}   label="Nama" type="text" placeholder="Nama Siswa"        className={inputClassName(formik?.errors?.nama)} />
                                                <ErrorMessage name="nama" component="span" className="p-error" />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Jenkel *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <Dropdown  name="jenkel"     optionLabel="label" optionValue="value" value={formik.values.jenkel} onChange={formik.handleChange} options={app.menus.jenkelItems} label="Jenkel"  placeholder="Pilih Jenis Kelamin"  className={inputClassName(formik?.errors?.jenkel)}   />
                                                <ErrorMessage name="jenkel" component="span" className="p-error" />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Kelas *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <DataSource   apiPath="components_data/kelas_option_list" >
                                                    {
                                                    ({ response  }) => 
                                                    <>
                                                    <Dropdown  name="kelas"     optionLabel="label" optionValue="value" value={formik.values.kelas} onChange={formik.handleChange} options={response} label="Kelas"  placeholder="Pilih kelas"  className={inputClassName(formik?.errors?.kelas)}   />
                                                    <ErrorMessage name="kelas" component="span" className="p-error" />
                                                    </>
                                                    }
                                                </DataSource>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Jurusan *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <DataSource   apiPath="components_data/jurusan_option_list" >
                                                    {
                                                    ({ response  }) => 
                                                    <>
                                                    <Dropdown  name="jurusan"     optionLabel="label" optionValue="value" value={formik.values.jurusan} onChange={formik.handleChange} options={response} label="Jurusan"  placeholder="Pilih Jurusan"  className={inputClassName(formik?.errors?.jurusan)}   />
                                                    <ErrorMessage name="jurusan" component="span" className="p-error" />
                                                    </>
                                                    }
                                                </DataSource>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Wali Kelas *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <DataSource   apiPath="components_data/wali_kelas_option_list" >
                                                    {
                                                    ({ response  }) => 
                                                    <>
                                                    <Dropdown  name="wali_kelas"     optionLabel="label" optionValue="value" value={formik.values.wali_kelas} onChange={formik.handleChange} options={response} label="Wali Kelas"  placeholder="Pilih Wali Kelas"  className={inputClassName(formik?.errors?.wali_kelas)}   />
                                                    <ErrorMessage name="wali_kelas" component="span" className="p-error" />
                                                    </>
                                                    }
                                                </DataSource>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="formgrid grid">
                                            <div className="col-12 md:col-3">
                                                Foto Siswa *
                                            </div>
                                            <div className="col-12 md:col-9">
                                                <div className={inputClassName(formik?.errors?.foto_siswa)}>
                                                    <Uploader name="foto_siswa" showUploadedFiles value={formik.values.foto_siswa} uploadPath="fileuploader/upload/foto_siswa" onChange={(paths) => formik.setFieldValue('foto_siswa', paths)} fileLimit={1} maxFileSize={2048} accept=".jpg,.png,.gif,.jpeg" multiple={false} label="Choose files or drop files here" onUploadError={(errMsg) => app.flashMsg('Upload error', errMsg, 'error')} />
                                                </div>
                                                <ErrorMessage name="foto_siswa" component="span" className="p-error" />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                { props.showFooter && 
                                <div className="text-center my-3">
                                    <Button onClick={(e) => handleSubmit(e, formik)} className="p-button-primary" type="submit" label="Simpan" icon="pi pi-send" loading={saving} />
                                </div>
                                }
                            </Form>
                            </>
                            }
                            </Formik>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </main>
		);
	}
}

//page props and default values
SiswaAddPage.defaultProps = {
	primaryKey: 'id_siswa',
	pageName: 'siswa',
	apiPath: 'siswa/add',
	routeName: 'siswaadd',
	submitButtonLabel: "Simpan",
	formValidationError: "Form is invalid",
	formValidationMsg: "Please complete the form",
	msgTitle: 'Create Record',
	msgAfterSave: "Record added successfully",
	msgBeforeSave: "",
	showHeader: true,
	showFooter: true,
	redirect: true,
	isSubPage: false
}
export default SiswaAddPage;
