import { Button } from 'primereact/button';
import { ImageViewer } from 'components/ImageViewer';
import { Menubar } from 'primereact/menubar';
import { PageRequestError } from 'components/PageRequestError';
import { ProgressSpinner } from 'primereact/progressspinner';
import { Title } from 'components/Title';
import SiswaEditPage from 'pages/siswa/Edit';
import useApp from 'hooks/useApp';

import useViewPage from 'hooks/useViewPage';
const SiswaViewPage = (props) => {
		const app = useApp();
	const pageController = useViewPage(props);
	const { item, pageReady, loading, apiRequestError, deleteItem } = pageController;
	const pageExportFormats =  [
		'pdf'
	];
	function ActionButton(data){
		const items = [
		{
			label: "Edit",
			command: (event) => { app.openPageDialog(<SiswaEditPage isSubPage apiPath={`/siswa/edit/${data.id_siswa}`} />, {closeBtn: true }) },
			icon: "pi pi-pencil"
		},
		{
			label: "Delete",
			command: (event) => { deleteItem(data.id_siswa) },
			icon: "pi pi-trash"
		}
	]
		return (<Menubar className="p-0 " model={items} />);
	}
	function PageFooter() {
		if (props.showFooter) {
			return (
				<div className="flex justify-content-between">
				</div>
			);
		}
	}
	if(loading){
		return (
			<div className="p-3 text-center">
				<ProgressSpinner style={{width:'50px', height:'50px'}} />
			</div>
		);
	}
	if(apiRequestError){
		return (
			<PageRequestError error={apiRequestError} />
		);
	}
	if(pageReady){
		return (
			<div>
<main id="SiswaViewPage" className="main-page">
    { (props.showHeader) && 
    <section className="page-section mb-3" >
        <div className="container">
            <div className="grid align-items-center">
                { !props.isSubPage && 
                <div className="col-fixed " >
                    <Button onClick={() => app.navigate(-1)} label=""  className="p-button p-button-text " icon="pi pi-arrow-left"  />
                </div>
                }
                <div className="col " >
                    <Title title="Siswa Details"   titleClass="text-2xl text-primary font-bold" subTitleClass="text-sm text-500"      separator={false} />
                </div>
            </div>
        </div>
    </section>
    }
    <section className="page-section " >
        <div className="container">
            <div className="grid ">
                <div className="col comp-grid" >
                    <div >
                        {/*PageComponentStart*/}
                        <div className="mb-3 grid justify-content-start">
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Nis</div>
                                        <div className="font-bold">{ item.nis }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Nama Siswa</div>
                                        <div className="font-bold">{ item.nama }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Jenkel</div>
                                        <div className="font-bold">{ item.jenkel }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Kelas</div>
                                        <div className="font-bold">{item.kelas && <Button className="p-button-text" icon="pi pi-eye" label={item.kelas_nama_kelas} to={`/kelas//${item.kelas}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Jurusan</div>
                                        <div className="font-bold">{item.jurusan && <Button className="p-button-text" icon="pi pi-eye" label={item.jurusan_nama_jurusan} to={`/jurusan//${item.jurusan}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Wali Kelas</div>
                                        <div className="font-bold">{item.wali_kelas && <Button className="p-button-text" icon="pi pi-eye" label={item.walikelas_nama_walikelas} to={`/walikelas//${item.wali_kelas}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Foto Siswa</div>
                                        <div className="font-bold"><ImageViewer imageSize="small" previewSize="" src={item.foto_siswa} width="auto" height="auto" numDisplay={1} style={{maxWidth:'100%'}} /></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="flex justify-content-between">
                            {props.exportButton && <Button icon="pi pi-print" className="mx-xs" title="Export" /> }
                            <div className="flex justify-content-start">
                                {ActionButton(item)}
                            </div>
                        </div>
                        {/*PageComponentEnd*/}
                    </div>
                </div>
            </div>
        </div>
    </section>
</main>
				<PageFooter />
			</div>
		);
	}
}
SiswaViewPage.defaultProps = {
	id: null,
	primaryKey: 'id_siswa',
	pageName: 'siswa',
	apiPath: 'siswa/view',
	routeName: 'siswaview',
	msgBeforeDelete: "Are you sure you want to delete this record?",
	msgTitle: 'Delete Record',
	msgAfterDelete: "Record deleted successfully",
	showHeader: true,
	showFooter: true,
	exportButton: true,
	isSubPage: false,
}
export default SiswaViewPage;
